.profile-container {
  width: 100%;
  height: 50%;
  margin-bottom: 5%;
}

.profile-picture {
  width: 60%;
  height: 75%;
  margin: 12.5% 20% 12.5% 20%;
}

.profile-picture-container {
  width: 50%;
  height: 100%;
  display: inline-block;
}

.profile-description-container {
  width: 50%;
  height: 100%;
  display: inline-block;
  vertical-align: top;
}

.profile-description {
  padding: 12.5% 0% 12.5% 12.5%;
}

.title {
  /* text-align: center; */
}

.headline {
  text-align: center;
}

.statement {
  height: 50%;
  background: -webkit-gradient(linear, left top, right top, from(#71c657), to(#e6f03c));
  background: -webkit-linear-gradient(left, #71c657, #e6f03c);
  background: -o-linear-gradient(left, #71c657, #e6f03c);
  background: linear-gradient(to right, #71c657, #e6f03c);
  margin-bottom: 5%;
}



